.tread_main {
    display: flex;
    width: 95%;
    flex-direction: column;
    margin-bottom: 5.5em;
    margin-top: 4em;
  }
  .treads_show_main {
    display: flex;
    width: 95%;
    flex-direction: column;
    margin-bottom: 5.5em;
    align-items: flex-start;
  }
  .tread_item {
    display: flex;
    text-decoration: none;
    flex-direction: row;
    margin-top: 1em;
    overflow: hidden;
    border-bottom: 1px solid rgba(0, 0, 0, 0.24);
    padding-bottom: 1em;
    width: 100%;
    color: #000;
  }
  .tread_item_img {
    border-radius: 5px;
    display: block;
    width: 4em;
    height: 4em;
    margin-right: 1em;
  }
  .tread_item_name {
    font-weight: bold;
    margin-bottom: 1em;
  }