.contacts {
    display: flex;
    width: 15%;
    min-height: 100%;
    position: fixed;
    justify-content: center;
    left: 0;
    background: linear-gradient(to bottom, #323232 0%, #3f3f3f 40%, #1c1c1c 150%),
      linear-gradient(
        to top,
        rgba(255, 255, 255, 0.4) 0%,
        rgba(0, 0, 0, 0.25) 200%
      );
    background-blend-mode: multiply;
    z-index: 1;
    bottom: 0;
    top: 0;
    overflow: overlay;
  }
  .contacts::-webkit-scrollbar {
    display: none;
  }
  
  .contacts .container_cont {
    display: flex;
    width: 85%;
    z-index: 3;
    margin-top: .9em;
    flex-direction: column;
    align-content: center;
    align-items: center;
  }

  @media (max-width: 1600px) {
    .contacts {
      width: 15%;
    }
    .contacts_back {
      width: 15%;
    }
  }

  @media (max-width: 1400px) {
    .contacts {
      width: 17%;
    }
    .contacts_back {
      width: 17%;
    }
}

@media (max-width: 1200px) {

    .contacts {
      width: 19%;
    }
    .contacts_back {
      width: 19%;
    }
}

@media (max-width: 1000px) {
    .contacts {
      width: 21%;
      display: flex;
    }
    .contacts_back {
      width: 21%;
    }
    .contacts .container {
      width: 95%;
    }
}

@media (max-width: 750px) {
    .contacts {
      width: 22%;
      display: flex;
    }
    .contacts_back {
      width: 22%;
    }
    .contacts .container {
      width: 95%;
    }
}

@media (max-width: 650px) {
    .contacts {
      width: 22%;
      display: flex;
    }
    .contacts_back {
      width: 22%;
    }
    .contacts .container {
      width: 95%;
    }
}

@media (max-width: 649px) {
    .contacts {
      width: 40%;
      display: flex;
      transform: translate(-100%);
      transition: 0.5s;
    }
    .contacts_back {
      width: 100%;
    }
}

@media (max-width: 500px) {
    .contacts {
      width: 50%;
      display: flex;
      transform: translate(-100%);
      transition: 0.5s;
    }
    .contacts_back {
      width: 100%;
    }
}