.bio_inputs {
    width: 80%;
    padding: 0.4em;
    outline: none;
    background: rgba(0, 0, 0, 0.0001);
    border: 0.5px solid rgba(0, 0, 0, 0.24);
    box-sizing: border-box;
    border-radius: 5px;
    font-size: 1em;
    margin: 0.5em 0;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.24);
  }

  .reg_active,
.reg_unactive {
  color: rgba(0, 0, 0, 0.4);
  margin: 0.4em 0;
  transition: 0.1s;
  font-size: 1em;
}
.reg_active:hover,
.reg_unactive:hover {
  color: rgba(0, 0, 0, 0.7);
}

.log_but,
.reg_but {
  background: #3577ef;
  border: 0;
  padding: 0.5em 2em;
  font-size: 1em;
  color: #fff;
  border-radius: 0.3em;
  font-weight: bold;
  transition: 0.2s;
  border: 1px solid #3577ef;
  margin: 0.8em 0;
  text-decoration: none;
  text-decoration: none;
  
}
.log_but:hover,
.reg_but:hover {
  background: #ffffff;
  color: #3577ef;
}

.choose_img_dt {
    display: flex;
    width: 70%;
    justify-content: space-around;
  }
  .choose_img_dt img {
    border-radius: 0.5em;
    width: 2.5em;
    height: 2.5em;
  }

/* Mobile */



  .login_window {
    width: 20%;
    background: #ffffff;
    z-index: 5;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -250%);
    transition: 1s;
    border-radius: 1em;
    display: flex;
    flex-direction: column;
    border: 2px solid #3577ef;
    align-items: center;
    justify-content: center;
    padding-bottom: 1em;
    display: none;
  }
  .close_login {
    position: absolute;
    top: 1em;
    right: 1em;
    opacity: 0.6;
  }
  .hide_window {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #000000;
    display: flex;
    position: fixed;
    z-index: 4;
    display: none;
    transition: 1s;
    opacity: 0.5;
    top: 0;
    left: 0;
    right: 0;
  }
  .login_window .container {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  .login_window input {
    width: 60%;
    padding: 0.4em;
    outline: none;
    background: rgba(0, 0, 0, 0.0001);
    border: 0.5px solid rgba(0, 0, 0, 0.24);
    box-sizing: border-box;
    border-radius: 5px;
    font-size: 1em;
    margin: 0.5em 0;
  }

  .choose_img {
    display: flex;
    width: 70%;
    justify-content: space-around;
  }
  .choose_img img {
    border-radius: 0.5em;
    width: 2.5em;
    height: 2.5em;
  }

  @media (max-width: 650px) {
    .login_window {
        width: 55%;
        transform: translate(-50%, -250%);
      }
      .login_window input {
        width: 70%;
      }
  }

  @media (max-width: 500px) {
    .login_window {
        width: 65%;
        transform: translate(-50%, -270%);
        display: flex;
      }


  }