.register_bio{
    display: flex;
    width: 100%;
    height: 100%;
  
    justify-content: center;
    align-content: center;
    align-items: center;
    position: fixed;
    
    right: 0;
    top: 0;
  }
  .register_bio .container{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    flex-direction: column;
    padding-top: 30%;
    overflow: overlay;
    background: rgba(0, 0, 0, 0.15);
    
  }

  /* mobile */


  .mobile_bio {
    background: #ffffff;
    width: 100%;
    height: 100%;
    display: flex;
    position: fixed;
    justify-content: center;
  }
  .mobile_bio .container_cont_m {
    display: flex;
    align-items: center;
    align-content: center;
    flex-direction: column;
  }