.nomad_list {
    color: #fff;
    font-family: Lato;
    font-size: 1.6em;
    display: flex;
    justify-content: space-between;
    width: 90%;
    align-items: center;
    font-weight: bold;
    position: relative;
  }
  .back_menu {
    display: none;
  }
  
  .enter_but {
    background: #3577ef;
    border: 0;
    padding: 0.3em 1em;
    font-size: 0.6em;
    color: #fff;
    border-radius: 0.3em;
    font-weight: bold;
    transition: 0.2s;
  }
  .enter_but:hover {
    background: #ffffff;
    color: #3577ef;
  }

  @media (max-width: 649px) {
    .nomad {
        display: none;
      }
  }