/* News block */

.news_main {
    display: flex;
    width: 95%;
    flex-direction: column;
    margin-bottom: 5.5em;
    align-items: flex-start;
    margin-top: 3em;
  }



  @media (max-width: 1400px) {
    .news_main {
        margin-bottom: 4.5em;
      }
  }


  /* post block */

  .post {
    display: flex;
    align-items: center;
    flex-direction: column;
    align-items: center;
    margin-top: 1em;
    overflow: hidden;
  }
  .post_body {
    display: flex;
  }
  
  .post_img {
    border-radius: 5px;
    display: block;
    width: 3em;
    height: 3em;
    margin-right: 1em;
  }
  .person_name {
    display: flex;
    font-weight: bold;
  }
  .person_post_date {
    font-weight: 400;
    margin-left: 0.8em;
    color: rgba(0, 0, 0, 0.6);
  }
  .person_content {
    margin-top: 0.4em;
  }
  .post_time {
    margin-top: 1em;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    color: rgba(0, 0, 0, 0.4);
  }
  
  .post_time::before,
  .post_time::after {
    content: "";
    width: 50%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.2);
    padding: 0 1em;
    margin: 0 1em;
  }