.message_in {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 73%;
    position: fixed;
    bottom: 0;
    padding: 2.5em 0;
    border-top: 2px solid rgba(0, 0, 0, 0.1);
    background: #fff;
    margin-top: 1em;
  }
  .message_in .block {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    position: absolute;
  }
  .message_left {
    display: flex;
    width: 89%;
    align-items: center;
  }
  .message_right {
    display: flex;
    width: 11%;
    align-items: center;
    justify-content: center;
    font-size: 1.9em;
  }
  .message_send {
    color: #646464;
    transition: 0.1s;
  }
  .message_send:hover {
    color: #3577ef;
  }
  .message_input {
    padding: 0 1em;
    font-size: 1.2em;
    color: rgba(0, 0, 0, 0.5);
  }
  .message_input {
    outline: none;
    border: 0;
    padding: 0.5em;
    position: relative;
    width: 40%;
    margin-left: 2em;
  }
  .message_input:focus {
    top: 1px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }

  @media (max-width: 1600px) {
    .message_in {
        width: 71%;
        padding: 2.5em 0;
      }
  }

  @media (max-width: 1400px) {
    .message_in {
        width: 68%;
        padding: 2em 0;
      }
  }

  @media (max-width: 1200px) {
    .message_in {
        width: 65%;
        padding: 2em 0;
      }
  }

  @media (max-width: 1000px) {
    .message_in {
        width: 61%;
        padding: 2em 0;
      }
      .message_input {
        width: 60%;
      }
  }

  @media (max-width: 750px) {
    .message_in {
        width: 58%;
      }
  }

  @media (max-width: 649px) {
    .message_in {
        width: 75%;
        padding: 2em 0;
      }
      .message_left {
        display: flex;
        width: 80%;
        align-items: center;
      }
      .message_right {
        display: flex;
        width: 20%;
        align-items: center;
        justify-content: center;
        font-size: 2em;
      }
  }

  @media (max-width: 500px) {
    .message_in {
        width: 100%;
        padding: 2em 0;
      }
  }